import axios from "axios";
import { axiosConfig, transformResponse } from ".";
import { SKIP_USER_EXISTS_CHECK } from "@/constants";

import {
  ILoginArgs,
  ILoginResponseSuccess,
  IServerResponse,
  ICreateUserArgs,
  IAnyExistsResponseSuccess
} from "@/shared/types";

export const login = async (args: ILoginArgs) => {
  const response = await axios.post<IServerResponse<ILoginResponseSuccess>>(
    "/user/login",
    args,
    { ...axiosConfig }
  );
  return transformResponse(response);
};

export const logout = async () => {
  // TODO
  const response = await axios.post<IServerResponse>(
    "/users/logout",
    {},
    { ...axiosConfig }
  );
  return transformResponse(response);
};

export const createUser = async (args: ICreateUserArgs) => {
  const response = await axios.post<IServerResponse>("/user/register", args, {
    ...axiosConfig
  });
  return transformResponse(response);
};

export const anyUserExists = async (): Promise<
  IServerResponse<IAnyExistsResponseSuccess>
> => {
  if (SKIP_USER_EXISTS_CHECK) {
    return { payload: { value: true }, messages: [] };
  }
  const response = await axios.get<IServerResponse<IAnyExistsResponseSuccess>>(
    "/user/anyExists",
    {
      ...axiosConfig
    }
  );
  return transformResponse(response);
};
