










import { ThemeUpdaterMixin } from "./mixins/themeUpdater";
import { LanguageUpdaterMixin } from "./mixins/languageUpdater";
import TheHeader from "./components/misc/TheHeader.vue";
import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import {
  routerModule,
  repositoriesModule,
  jobsModule,
  authModule
} from "@/store/modules";

@Component({
  components: {
    TheHeader
  },
  metaInfo: {
    titleTemplate: "Backup380 - %s",
    title: "Running"
  }
})
export default class AppComponent extends Mixins(
  ThemeUpdaterMixin,
  LanguageUpdaterMixin
) {
  get currentPath() {
    return routerModule.fullPath;
  }

  get isLoggedIn() {
    return authModule.isLoggedIn;
  }

  @Watch("isLoggedIn")
  async onLoggedInChange() {
    // TODO: maybe move this
    if (this.isLoggedIn) {
      await Promise.all([
        repositoriesModule.fetchAllRepositories(),
        jobsModule.fetchAllJobs()
      ]);
    }
  }
}
